.chatWrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    background-color: #f5f5f5;
    border-radius: 5px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    overflow: hidden;
    padding: 10px;
    max-height: 200px;
    overflow-y: auto;
}

.chatHistory {
    padding: 20px;
}

.chatRow {
    font-size: 14px;
    margin-bottom: 5px;
}

.chatRow span {
    font-weight: 600;
}