.menuItem {
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
    width: 100%;
    align-items: center;
    display: flex;
    padding: 10px;
    cursor: pointer;
    margin-bottom: 10px;
}

.menuItem img {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

.menuItemTitle {
    font-size: 14px;
    font-weight: 500;
}

.menuItemDescription {
    font-size: 12px;
    color: #a0a0a0;
}

.menuItem>div:first-child {
    display: flex;
}