.chatContainer {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    position: fixed;
    bottom: 110px;
    right: 30px;
    min-width: 350px;
    max-width: 350px;
    border-radius: 12px;
}

.chatHeader {
    background: #446ee7;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}

.chatHeader p {
    color: white;
    font-weight: 600;
}

.chatSearch {
    margin-top: 20px;
}

.chatSearch input {
    width: 100%;
    padding: 10px;
    border-radius: 6px;
    border: 1px solid #eaeaea;
    outline: none;
}

.chatMenu {
    padding: 20px;
    padding-top: 0px;
    padding-bottom: 10px;
}

.chatSearch {
    display: flex;
    align-items: center;
}

.chatSearch button {
    background: #446ee7;
    color: white;
    padding: 10px;
    border-radius: 6px;
    border: none;
    outline: none;
    margin-left: 10px;
    cursor: pointer;
}

.chatMenu {
    padding-top: 20px;
}