* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}


/* MENU ICON */

.menuIcon {
    position: fixed;
    right: 30px;
    bottom: 30px;
    padding: 15px;
    border-radius: 100px;
    background: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    width: 60px;
    height: 60px;
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
}

.menuIcon svg {
    fill: #446ee7;
    stroke: #446ee7;
}